.img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    padding: 5%;
    display: block;
    margin: 0 auto;
    object-fit: fill;
}

.content {
    background-color: black;
    width: 32%;
    color: #fff;
    text-align: center;
    display: inline-block;
    margin-left: 10px;
}

.main-row {
    margin: 10px !important;
}
.column {
    width: 100% !important;
    font-style: italic !important;
    height: auto;
    border-radius: 5px;
    border: 2px solid #fff;
}
#contact{
    margin:2% auto;
}
#Card{
    border: none;
}
.about {
    line-height: 1.5rem;
    padding: 10px;
}

.about-img {
    margin-top: 5%;
    width: 100% !important;
    height: auto;
}

#about {
    padding: 4%;
}


#try {
    border-radius: 50%;
}
.img-circle{
    width: 60px;
    height: 60px;
    border-radius:50%;
    align-items: center;
    margin:0 auto;
}
@media screen and (min-width: 900px) {
    #Card {
        height: auto !important;
    }
    .bg-danger{
        height:auto !important;
    }
}
@media screen and (max-width: 800px) {
    .bg-danger {
        height: auto !important;
    }
    #Card {
        height: auto !important;
    }

    .about-row {
        width: 100%;
        display: flex !important;
        justify-content: center;
        flex-direction: column !important;
    }
}
@media screen and (max-width:725px) {
    .bg-danger {
        height: auto !important;
    }
    .img-fluid {
        margin:0 auto;
        width:20%;
        height:auto;
    }
    #contact-row {
        display: grid;
        grid-template-rows: auto auto auto;
        justify-content: center;
        grid-gap: 20px;
    }
}

@media screen and (max-width:653px) {
    .bg-danger {
        height: auto !important;
    }
    .img-fluid {
        margin: 0 auto;
        width: 20%;
        height: auto;
    }
    #contact-row {
        display: grid;
        grid-template-rows: auto auto auto;
        justify-content: center;
        grid-gap: 20px;
    }
}